import React, { useState } from "react";
import { Button, Box, TextField, FormControl, Select, MenuItem, Chip, InputLabel, OutlinedInput } from '@material-ui/core'
import { contents } from "../contents";

const defaultValues = {
    topic: '',
    contents: {
        subject: '',
        content: ''
    },
    students: {
        name: '',
        class: 0
    },
    date: '',
    duration: ''
}

const LessonForm = () => {
    const [formValues, setFormValues] = useState(defaultValues)
    const [personName, setPersonName] = React.useState([]);

    const handleChange = (event) => {
      const {
        target: { value },
      } = event;
      setPersonName(
        // On autofill we get a the stringified value.
        typeof value === 'string' ? value.split(',') : value,
      );
    };

    const handleSubmit = (event) => {
        event.preventDefault()
        console.log(formValues)
    }

    return (
        <form onSubmit={handleSubmit} >
            <Box display='flex' flexDirection='column'>
                <Box display='flex' flexDirection='column' justifyContent='flex-start' padding='20px'>
                    <Box padding='20px'>
                        Topic
                        <TextField 
                            id='topic-input'
                            label='topic'
                            variant='filled'
                            autoFocus
                            required 
                            fullWidth
                            className='topic'
                        />
                    </Box>
                    <Box padding='20px' display='flex' flexDirection='column'>
                        Subject
                        <FormControl>
                            <Select
                                defaultValue=''
                             MenuProps={{
                                anchorOrigin: {
                                  vertical: "bottom",
                                  horizontal: "left"
                                },
                                transformOrigin: {
                                  vertical: "top",
                                  horizontal: "left"
                                },
                                getContentAnchorEl: null
                              }}
                                name='subject'
                            >
                                <MenuItem key='finnish' value='finnish'>
                                    Finnish
                                </MenuItem>
                                <MenuItem key='maths' value='maths'>
                                    maths
                                </MenuItem>
                                <MenuItem key='science' value='science'>
                                    science
                                </MenuItem>
                            </Select>
                        </FormControl>
                    </Box>
                    <Box padding='20px' display='flex' flexDirection='column'>
                        Content
                        <FormControl sx={{ m: 1, width: 300 }}>
                            <Select
                            MenuProps={{
                                anchorOrigin: {
                                  vertical: "bottom",
                                  horizontal: "left"
                                },
                                transformOrigin: {
                                  vertical: "top",
                                  horizontal: "left"
                                },
                                getContentAnchorEl: null
                              }}
                            labelId="demo-multiple-chip-label"
                            id="demo-multiple-chip"
                            multiple
                            value={personName}
                            onChange={handleChange}
                            renderValue={(selected) => (
                                <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                                {selected.map((value) => (
                                    <Chip key={value} label={value} />
                                ))}
                                </Box>
                            )}
                            >
                            {contents.map((name) => (
                                <MenuItem
                                key={name}
                                value={name}
                                >
                                {name}
                                </MenuItem>
                            ))}
                            </Select>
                        </FormControl>
                    </Box>
                </Box>
            </Box>
            <Box display='flex' justifyContent='center' paddingTop='20%'>
                <Button variant='contained' type='submit'>
                    Submit
                </Button>
            </Box>
        </form>
    )
    
}

export default LessonForm